@import url('https://fonts.googleapis.com/css?family=Playfair+Display&display=swap');

html, body {
    width: 99%;
    height: 97%;
}

body {
    background-color: #222222;
    color: #666666;
    font-family: 'Playfair Display', serif;
}

h1 {
    color: #d1d1d1;
    margin: 25px;
}

footer {
    margin-bottom: 10px;
    text-align: right;
    font-size: 12px;
}

p {
    /*text-indent: 30px;*/
    line-height: 1.5;
}

.flex-container1 {
    min-height: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.flex-container2 {
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: flex-start;
}

#nav {
    flex-basis: 100%;
    margin-left: 10px;
    font-size: 20px;
}

#map {
    /*border: 1px #ccc solid;*/
    flex: 2;
    margin: 40px;
    width: 50%;
}

#info {
    flex: 1;
    margin: 40px;
    width: 50%;
}

#select {

}

