a:link {
    color: #888888;
    text-decoration: none;
}

a:visited {
    color: #888888;
    text-decoration: none;
}

a:hover {
    color: #d1d1d1;
}

#mapv1 {
    width: 1000px;
    height: 500px;
    border: 3px solid #d1d1d1;
}

.water {
    fill: #002fff;
    fill-opacity: 0.5;
}